import React, { useState, useEffect } from 'react';
import { Bar } from 'react-chartjs-2';

function EmotionChart() {
  const [chartData, setChartData] = useState({
    labels: [],
    datasets: [
      {
        label: 'Emotion Log',
        data: [],
        backgroundColor: 'rgba(75, 192, 192, 0.2)',
        borderColor: 'rgba(75, 192, 192, 1)',
        borderWidth: 1,
      },
    ],
  });
  const [days, setDays] = useState(7);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchEmotionData = async () => {
      setLoading(true);
      setError(null);

      try {
        const response = await fetch(`http://localhost:8000//api/emotion-log-chart/${days}/`, {
          method: 'GET',
          credentials: 'include',
        });

        if (!response.ok) {
          throw new Error('Error fetching emotion data');
        }

        const data = await response.json();

        // Example transformation
        const labels = data.map(entry => entry.date);
        const emotionValues = data.map(entry => entry.emotion_count);

        setChartData({
          labels: labels,
          datasets: [
            {
              label: 'Emotion Log',
              data: emotionValues,
              backgroundColor: 'rgba(75, 192, 192, 0.2)',
              borderColor: 'rgba(75, 192, 192, 1)',
              borderWidth: 1,
            },
          ],
        });
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchEmotionData();
  }, [days]);

  const handleDaysChange = (event) => {
    const value = parseInt(event.target.value, 10);
    if ([7, 30].includes(value)) {
      setDays(value);
    }
  };

  if (loading) {
    return <p>Loading chart...</p>;
  }

  if (error) {
    return <p>Error: {error}</p>;
  }

  return (
    <div>
      <h1>Emotions Over Time</h1>
      <select onChange={handleDaysChange} value={days}>
        <option value={7}>Last 7 Days</option>
        <option value={30}>Last 30 Days</option>
      </select>

      <Bar
        data={chartData}
        options={{
          responsive: true,
          plugins: {
            tooltip: {
              enabled: true,
              callbacks: {
                label: (context) => `${context.label}: ${context.raw}`,
              },
            },
          },
          scales: {
            x: {
              title: {
                display: true,
                text: 'Date',
              },
            },
            y: {
              title: {
                display: true,
                text: 'Number of Entries',
              },
              beginAtZero: true,
            },
          },
          animation: {
            duration: 1000,
          },
        }}
      />
    </div>
  );
}

export default EmotionChart;
