import React, { useState } from 'react';
import AuthForm from './AuthForm'; // Import the AuthForm component
import './LandingPage.css'; // Optional CSS for styling

function LandingPage() {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const openModal = () => setIsModalOpen(true);
  const closeModal = () => setIsModalOpen(false);

  const offerings = [
    { title: 'Track Your Mood', content: 'Monitor stress, anxiety and mood daily.' },
    { title: 'Personalised Journal', content: 'Write and reflect with guided prompts.' },
    { title: 'Professional Support', content: 'Easily connect with mental health providers.' },
  ];

  const [activeTab, setActiveTab] = useState(0);

  return (
    <div className="landing-page">
      {/* Hero Section */}
      <section className="hero">
        <h1>Your Mental Health Simplified</h1>
        <p>Track your journey, connect with professionals, and achieve wellness with ease.</p>
        <button onClick={openModal} className="sign-up-button">Get Started</button>
      </section>

      {/* What We Offer */}
      <section className="offerings">
        <h2>What We Offer</h2>
        <div className="tabs">
          {offerings.map((offer, index) => (
            <button
              key={index}
              className={`tab ${activeTab === index ? 'active' : ''}`}
              onClick={() => setActiveTab(index)}
            >
              {offer.title}
            </button>
          ))}
        </div>
        <div className="tab-content">
          <p>{offerings[activeTab].content}</p>
        </div>
      </section>

      {/* Sign-Up Modal */}
      {isModalOpen && (
        <div className="modal-overlay open">
          <div className="modal open">
            <button className="close-modal" onClick={closeModal} aria-label="Close modal">X</button>
            <AuthForm formType="signup" onClose={closeModal} onAuthSuccess={() => setIsModalOpen(false)} />
          </div>
        </div>
      )}
    </div>
  );
}

export default LandingPage;
