import React, { useState, useEffect } from 'react';

function JournalEntries() {
  const [entries, setEntries] = useState([]);
  const [textContent, setTextContent] = useState('');
  const [image, setImage] = useState(null);

  useEffect(() => {
    // Fetch existing journal entries
    const fetchEntries = async () => {
      const response = await fetch('/api/journal-entries/', {
        method: 'GET',
        credentials: 'include', // This ensures session-based auth is used
      });
      if (response.ok) {
        const data = await response.json();
        setEntries(data);
      }
    };
    fetchEntries();
  }, []);

  const handleAddJournalEntry = async () => {
    if (!textContent) return;

    const formData = new FormData();
    formData.append('text_content', textContent);
    if (image) formData.append('image', image);

    const response = await fetch('http://localhost:8000/api/journal-entries/', {
      method: 'POST',
      body: formData,
      credentials: 'include', // Ensure session-based auth is included
    });

    if (response.ok) {
      const newEntry = await response.json(); // Assume backend returns the newly created entry
      setEntries([newEntry, ...entries]);
      setTextContent(''); // Reset the text content after submission
      setImage(null); // Reset the image field
    }
  };

  return (
    <div>
      <h1>Journaling</h1>
      <textarea
        value={textContent}
        onChange={(e) => setTextContent(e.target.value)}
        placeholder="Write your journal entry..."
      />
      <input type="file" onChange={(e) => setImage(e.target.files[0])} />
      <button onClick={handleAddJournalEntry}>Add Journal Entry</button>

      <div>
        {entries.map((entry, index) => (
          <div key={index}>
            <p>{entry.text_content}</p>
            {entry.image && <img src={entry.image} alt={`Journal entry on ${entry.created_at}`} />}

          </div>
        ))}
      </div>
    </div>
  );
}

export default JournalEntries;
