import React, { useState, useEffect } from 'react';
import PatientRecords from './PatientRecords';

function DoctorDashboard() {
  const [appointments, setAppointments] = useState([]);
  const [newAppointment, setNewAppointment] = useState({ patient_id: '', date: '', notes: '' });
  const [appointmentNotes, setAppointmentNotes] = useState({});
  const [isFetching, setIsFetching] = useState(true);
  const [error, setError] = useState('');

  // Fetch appointments function
  const fetchAppointments = async () => {
    setIsFetching(true);
    try {
      const response = await fetch('/api/appointments/', {
        method: 'GET',
        credentials: 'include',
      });

      if (response.ok) {
        const data = await response.json();
        setAppointments(data);
      } else {
        setError('Error fetching appointments');
      }
    } catch (error) {
      setError('Error fetching appointments');
    } finally {
      setIsFetching(false);
    }
  };

  useEffect(() => {
    fetchAppointments();
  }, []);

  // Handle note change
  const handleNoteChange = (appointmentId, value) => {
    setAppointmentNotes((prev) => ({ ...prev, [appointmentId]: value }));
  };

  // Add new appointment
  const addAppointment = async () => {
    if (!newAppointment.patient_id || !newAppointment.date) {
      alert('Please fill in all fields');
      return;
    }

    try {
      const response = await fetch('http://localhost:8000/api/appointments/add_appointment/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(newAppointment),
        credentials: 'include',
      });

      if (response.ok) {
        const newAppointmentData = await response.json();
        setAppointments((prevAppointments) => [...prevAppointments, newAppointmentData]);
      } else {
        setError('Error adding appointment');
      }
    } catch (error) {
      setError('Error adding appointment');
    }
  };

  // Cancel appointment
  const cancelAppointment = async (id) => {
    try {
      const response = await fetch(`/api/appointments/${id}/cancel_appointment/`, {
        method: 'POST',
        credentials: 'include',
      });

      if (response.ok) {
        setAppointments((prevAppointments) =>
          prevAppointments.filter((appointment) => appointment.id !== id)
        );
      } else {
        setError('Error canceling appointment');
      }
    } catch (error) {
      setError('Error canceling appointment');
    }
  };

  // Add note to appointment
  const addAppointmentNote = async (appointmentId) => {
    try {
      const response = await fetch(`/api/appointment-notes/${appointmentId}/add_note/`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ note: appointmentNotes[appointmentId] }),
        credentials: 'include',
      });

      if (response.ok) {
        // Optionally, update the appointment notes state here for immediate UI update
      } else {
        setError('Error adding appointment note');
      }
    } catch (error) {
      setError('Error adding appointment note');
    }
  };

  return (
    <div>
      <h1>Doctor's Dashboard</h1>

      {/* Display error message */}
      {error && <p style={{ color: 'red' }}>{error}</p>}

      {/* Appointments Section */}
      <div>
        <h2>Appointments</h2>
        {isFetching ? (
          <p>Loading appointments...</p>
        ) : (
          appointments.map((appointment) => (
            <div key={appointment.id}>
              <h3>Appointment with {appointment.patient.user.username} on {appointment.date}</h3>
              <button onClick={() => cancelAppointment(appointment.id)}>Cancel Appointment</button>
              <div>
                <textarea
                  value={appointmentNotes[appointment.id] || ''}
                  onChange={(e) => handleNoteChange(appointment.id, e.target.value)}
                  placeholder="Add notes..."
                />
                <button onClick={() => addAppointmentNote(appointment.id)}>Add Note</button>
              </div>
            </div>
          ))
        )}
      </div>

      {/* Add New Appointment Section */}
      <div>
        <h2>Add New Appointment</h2>
        <input
          type="text"
          placeholder="Patient ID"
          value={newAppointment.patient_id}
          onChange={(e) => setNewAppointment({ ...newAppointment, patient_id: e.target.value })}
        />
        <input
          type="datetime-local"
          value={newAppointment.date}
          onChange={(e) => setNewAppointment({ ...newAppointment, date: e.target.value })}
        />
        <textarea
          value={newAppointment.notes}
          onChange={(e) => setNewAppointment({ ...newAppointment, notes: e.target.value })}
          placeholder="Notes"
        />
        <button onClick={addAppointment} disabled={isFetching}>
          {isFetching ? 'Adding Appointment...' : 'Add Appointment'}
        </button>
      </div>

      {/* Patient Records Section */}
      <PatientRecords />
    </div>
  );
}

export default DoctorDashboard;
