import React, { useState } from 'react';
import axios from 'axios';

function AuthForm({ formType, onClose, onAuthSuccess }) {
  const [formData, setFormData] = useState({
    username: '',
    password: '',
    email: '',
    age: '',
    gender: '',
  });

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const getCookie = (name) => {
    const match = document.cookie.match(new RegExp('(^| )' + name + '=([^;]+)'));
    return match ? match[2] : null;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    const endpoint = formType === 'signup'
      ? 'http://localhost:8000/api/signup/'
      : 'http://localhost:8000/api/login/';
    const dataToSend = formType === 'signup'
      ? { ...formData }
      : { username: formData.username, password: formData.password };

    const config = {
      headers: {
        'Content-Type': 'application/json',
        'X-CSRFToken': getCookie('csrftoken'),
      },
      withCredentials: true,
    };

    try {
      const response = await axios.post(endpoint, dataToSend, config);
      alert(response.data.message || 'Success!');
      onAuthSuccess();
      onClose();
    } catch (err) {
      setError(err.response?.data?.error || 'An error occurred');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={modalStyle}>
      <div style={formContainerStyle}>
        <h2>{formType === 'signup' ? 'Sign Up' : 'Login'}</h2>
        <form onSubmit={handleSubmit}>
          {formType === 'signup' && (
            <>
              <input
                type="email"
                name="email"
                placeholder="Email"
                value={formData.email}
                onChange={handleInputChange}
                style={inputStyle}
                required
              />
              <input
                type="number"
                name="age"
                placeholder="Age"
                value={formData.age}
                onChange={handleInputChange}
                style={inputStyle}
                required
              />
              <input
                type="text"
                name="gender"
                placeholder="Gender"
                value={formData.gender}
                onChange={handleInputChange}
                style={inputStyle}
                required
              />
            </>
          )}
          <input
            type="text"
            name="username"
            placeholder="Username"
            value={formData.username}
            onChange={handleInputChange}
            style={inputStyle}
            required
          />
          <input
            type="password"
            name="password"
            placeholder="Password"
            value={formData.password}
            onChange={handleInputChange}
            style={inputStyle}
            required
          />
          <button type="submit" style={buttonStyle} disabled={loading}>
            {loading ? 'Submitting...' : formType === 'signup' ? 'Sign Up' : 'Login'}
          </button>
        </form>
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <button onClick={onClose} style={closeButtonStyle}>
          Close
        </button>
      </div>
    </div>
  );
}

// Modal and form styles
const modalStyle = { /* Same as before */ };
const formContainerStyle = { /* Same as before */ };
const inputStyle = { /* Same as before */ };
const buttonStyle = { /* Same as before */ };
const closeButtonStyle = { /* Same as before */ };

export default AuthForm;
