import React, { useState } from 'react';
import AuthForm from './AuthForm';
import axios from 'axios';

function Header() {
  const [modalOpen, setModalOpen] = useState(false);
  const [formType, setFormType] = useState('login');
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  const openModal = (type) => {
    setFormType(type);
    setModalOpen(true);
  };

  const closeModal = () => {
    setModalOpen(false);
  };

  const handleAuthSuccess = () => {
    setIsAuthenticated(true);
    setModalOpen(false);
  };

  const handleLogout = async () => {
    try {
      const response = await axios.post(
        'http://localhost:8000/api/logout/',
        {},
        { withCredentials: true }
      );

      if (response.status === 200) {
        setIsAuthenticated(false);
      } else {
        console.error('Logout failed:', response.data);
      }
    } catch (err) {
      console.error('Logout error:', err);
    }
  };

  return (
    <header style={headerStyle}>
      <h1 style={logoStyle}>MHRecord</h1>
      <nav>
        {isAuthenticated ? (
          <button style={buttonStyle} onClick={handleLogout}>
            Logout
          </button>
        ) : (
          <>
            <button style={buttonStyle} onClick={() => openModal('login')}>
              Login
            </button>
            <button style={buttonStyle} onClick={() => openModal('signup')}>
              Sign Up
            </button>
          </>
        )}
      </nav>
      {modalOpen && <AuthForm formType={formType} onClose={closeModal} onAuthSuccess={handleAuthSuccess} />}
    </header>
  );
}

// Header styles
const headerStyle = { /* Same as before */ };
const logoStyle = { /* Same as before */ };
const buttonStyle = { /* Same as before */ };

export default Header;
