// EmotionLogs.js
import React, { useState, useEffect } from 'react';

function EmotionLogs() {
  const [emotionLogs, setEmotionLogs] = useState([]);
  
  // Fetch emotion logs from the backend when the component mounts
  useEffect(() => {
    const fetchEmotionLogs = async () => {
      try {
        const response = await fetch('http://localhost:8000//api/emotion-logs/', {
          method: 'GET',
          credentials: 'include',  // Ensures the session cookie is sent
        });

        if (response.ok) {
          const data = await response.json();
          setEmotionLogs(data);
        } else {
          console.error('Failed to fetch emotion logs');
        }
      } catch (error) {
        console.error('Error fetching emotion logs:', error);
      }
    };

    fetchEmotionLogs();
  }, []);  // Empty dependency array ensures it runs once on mount

  return (
    <div>
      <h2>Emotion Logs</h2>
      <table>
        <thead>
          <tr>
            <th>Date</th>
            <th>Emotion</th>
          </tr>
        </thead>
        <tbody>
          {emotionLogs.length === 0 ? (
            <tr>
              <td colSpan="2">No emotion logs found</td>
            </tr>
          ) : (
            emotionLogs.map((log, index) => (
              <tr key={index}>
                <td>{new Date(log.created_at).toLocaleString()}</td>
                <td>{log.emotion}</td>
              </tr>
            ))
          )}
        </tbody>
      </table>
    </div>
  );
}

export default EmotionLogs;
